import { Component, EventEmitter, Output, Renderer2 } from '@angular/core';
import { GetOwnUser } from '@backbase/data-ang/user';
import { Observable } from 'rxjs';
import { filter, switchMap, tap } from 'rxjs/operators';
import { ImpersonationDataService } from '../impersonation-data/impersonation-data.service';
import { ImpersonationService } from '../impersonation-service/impersonation.service';

@Component({
  selector: 'bb-impersonation-banner',
  templateUrl: 'banner.component.html',
})
export class ImpersonationBannerComponent {
  /**
   * Event Emitted when stop impersonation button is pressed.
   * Please logout the user as your app requires.
   */
  @Output() stopImpersonation = new EventEmitter();

  private readonly impersonatedClass = 'impersonated';
  readonly user$: Observable<GetOwnUser> = this.impersonationService.isImpersonated$.pipe(
    tap((state) => this.setImpersonationClass(state)),
    filter((state) => !!state),
    switchMap(() => this.dataService.getOwnUser()),
  );

  /** @internal */
  constructor(
    private readonly impersonationService: ImpersonationService,
    private readonly dataService: ImpersonationDataService,
    private readonly renderer: Renderer2,
  ) {}

  private readonly setImpersonationClass = (state: boolean) =>
    state
      ? this.renderer.addClass(document.body, this.impersonatedClass)
      : this.renderer.removeClass(document.body, this.impersonatedClass);
}
